<template>
  <div class="wrapper">
    <Header :progressOn="false" :rightButton="{name: 'BACK', location: this.changeScene,}"></Header>
    <div class="info fade-in">
      <Info></Info>
    </div>
  </div>
</template>

<script>
import Header from '@/components/Header';
import Info from '@/components/Info';

export default {
  name: 'InfoPage',
  components: {
    Header,
    Info,
  },
  data() {
    return {
      nextPage: false,
    };
  },
  methods: {
    changeScene() {
      if (this.nextPage === false) {
        setTimeout(() => {
          const wrapper = document.querySelector('.info');
          wrapper.classList.remove('fade-out');
          location.href = this.$EventBus.back;
        }, 600);

        const wrapper = document.querySelector('.info');
        wrapper.classList.remove('fade-in');
        wrapper.classList.add('fade-out');
        this.nextPage = true;
      }
    },
  },
};
</script>

<style scoped>
.wrapper {
  width: 100%;
  height: 100%;
}

.info {
  width: 100%;
  height: 100%;
}
</style>



