<template>
  <div class="text">
    <div class="animate">
      <p>포트폴리오가 궁금하신가요?</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TextLine',
};
</script>

<style scoped>
.text {
  position: absolute;
  transform: translate(320px, -300px);
}

.animate {
  animation: ping-pong 2s infinite ease-in-out alternate;
}

@keyframes ping-pong {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(50px);
  }
  100% {
    transform: translateY(0px);
  }
}

p {
  margin: 0;
  padding: 0;
  height: 0;
  width: 800px;

  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  font-size: 24px;
  font-weight: 300;
  letter-spacing: -1px;
  text-align: right;
  color: #888888;
}
</style>



